import React, {useEffect, useState} from 'react';
import styled from 'styled-components';
import axios from 'axios';

const CommunityBlock = styled.div`
    border-top:107px solid orange;
    padding:100px 0;
    .row{
        h2 { text-align:center; font-size:30px;}
    }
`;

const Communitysection = () => {

    const [cleanair, setCleanair] = useState([])

    const getAir = async () => {
        const response = await axios.get('http://apis.data.go.kr/B552584/UlfptcaAlarmInqireSvc/getUlfptcaAlarmInfo?serviceKey=3HbhhV9kf7M0aXKuIJQREJG2U%2FW4n7uidnNa4X%2BHk0OwDPepYARpAAjCM%2B9TEAHxtPZvOzlFm5fjMqkOulW82g%3D%3D&returnType=xml&numOfRows=100&pageNo=1&year=2020&itemCode=PM10')
        console.log(response)
        setCleanair(response)
        
    }

    useEffect( ()=>{
        getAir()
    }, [] )

    return (
        <CommunityBlock>
            <div className="row">
                <h2>에어코리아 미세먼지 경고발령 현황</h2>
                <ul>
                    {cleanair && cleanair.map( (data, index) => <li>{data}</li>)}
                </ul>
            </div>
        </CommunityBlock>
    );
};

export default Communitysection;